import React, { useContext, useEffect } from 'react';
import './Task3.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import speaker from '../assets/img/speaker_volume.svg';
import {
    sound_see,
    sound_bat,
    sound_witch,
    sound_with,
    sound_spider,
    sound_weaving,
    sound_ghost,
    sound_flying,
    sound_pumpkin,
    sound_lying,
    soundSource
} from './sound/index_sound.js'





// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p>Контент направленный на помощь в обучении</p>
        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Введение в урок. Песня «Halloween»!</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p>Look, listen and repeat</p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = 'Смотрите, слушайте и повторяйте';

function Task3() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, [])

    const chartsContent = [
        {
            id: 'chartsContent_1',
            mediaSource: sound_see,
            content: `see [siː] — видеть`
        },
        {
            id: 'chartsContent_2',
            mediaSource: sound_bat,
            content: `bat [bæt] — летучая мышь`
        },
        {
            id: 'chartsContent_3',
            mediaSource: sound_witch,
            content: `witch [wɪtʃ] — ведьма`
        },
        {
            id: 'chartsContent_4',
            mediaSource: sound_with,
            content: `with [wɪð] — с`
        },
        {
            id: 'chartsContent_5',
            mediaSource: sound_spider,
            content: `spider [ˈspaɪdə] — паук`
        },
        {
            id: 'chartsContent_6',
            mediaSource: sound_weaving,
            content: `weaving a net [wiːvɪŋ ə net] — плетущий паутину`
        },
        {
            id: 'chartsContent_7',
            mediaSource: sound_ghost,
            content: `ghost  [ɡəʊst] — привидение`
        },
        {
            id: 'chartsContent_8',
            mediaSource: sound_flying,
            content: `flying above the hat [ˈflaɪɪŋ əˈbʌv ðə hæt] — летящее над шляпой`
        },
        {
            id: 'chartsContent_9',
            mediaSource: sound_pumpkin,
            content: `pumpkin [ˈpʌm(p)kɪn] — тыква`
        },
        {
            id: 'chartsContent_10',
            mediaSource: sound_lying,
            content: `lying on the mat [ˈlaɪɪŋ ɒn ðə mæt] — лежащая на коврике`
        },
    ];

    const playSound = (media) => {
        let audio = new Audio(media);
        audio.play()
    }



    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="wrapperTask3">
                {chartsContent.map(item => (
                    <div key={item.id} className="chartsTask3" onClick={() => playSound(item.mediaSource)}>
                        <span style={{ marginRight: '15px' }} title="Воспроизвести"><img style={{ marginBottom: '-5px' }} src={speaker} alt="" /></span><span>{item.content}</span>
                    </div>
                ))}
            </div>

        </div>
    );
}

export default Task3;