import React, { useState, useContext, useEffect } from 'react';
import './Task15.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import halloweenSong from './media/Eng-2021_2_2_4_7.mp4';
import soundSource from './sound/023.mp3';


// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p>Контент направленный на помощь в обучении</p>
        </>
    );
};


// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Слушаем историю!  Listen to the story!</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p>Look and listen to the story.</p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = 'Посмотрите и послушайте историю';


function Task15() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, [])


    function handler(e) {
        e.target.style.backgroundColor = 'red';
    }


    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <video className="videoScreen" width="560" controls controlsList="nodownload">
                <source src={halloweenSong} type='video/mp4' />
            </video>
            {/* <iframe style={{ borderRadius: '10px', margin: '2rem 0 0 0' }} width="560" height="315" src="https://www.youtube.com/embed/4jxcWlq3CBg" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> */}


        </div>
    );
}

export default Task15;