import React, { useState, useContext, useEffect } from 'react';
import './Task5.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import soundSource from './sound/014.mp3';



// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p>Контент направленный на помощь в обучении</p>
        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Введение в урок. Песня «Halloween»!</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p>Match the English and the Russian words</p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = 'Сопоставьте английские и русские слова';

function Task5() {
    const [russianActive, setRussianActive] = useState(undefined);
    const [englishActive, setEnglishActive] = useState(undefined);


    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, [])



    const englishForMathing = [
        {
            data: 't5_hat',
            value: 'hat'
        },
        {
            data: 't5_weave',
            value: 'weave'
        },
        {
            data: 't5_net',
            value: 'net'
        },
        {
            data: 't5_above',
            value: 'above'
        },
        {
            data: 't5_with',
            value: 'with'
        },
        {
            data: 't5_mat',
            value: 'mat'
        },
    ];

    const russianForMathing = [
        {
            data: 't5_hat',
            value: 'шляпа'
        },
        {
            data: 't5_weave',
            value: 'плести'
        },
        {
            data: 't5_net',
            value: 'паутина'
        },
        {
            data: 't5_above',
            value: 'над'
        },
        {
            data: 't5_with',
            value: 'с'
        },
        {
            data: 't5_mat',
            value: 'коврик'
        },
    ];


    function onClickHandlerEnglish(event, index) {
        let targetChart = event.target;
        if (targetChart.classList.contains('activet5')) {
            targetChart.classList.remove('activet5')
        }
        else {
            targetChart.classList.add('activet5');
            if (targetChart != englishActive) {
                englishActive?.classList.remove('activet5');

            }
        }
        setEnglishActive(targetChart);
    }



    function onClickHandlerRussian(event, index) {
        let targetChart = event.target;
        if (targetChart.classList.contains('activet5')) {
            targetChart.classList.remove('activet5')
        }
        else {
            targetChart.classList.add('activet5');
            if (targetChart != russianActive) { russianActive?.classList.remove('activet5') }
        }
        setRussianActive(targetChart);

    }


    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="wrapperTask5">
                <div className="englishLeftColumn">
                    {englishForMathing.map((item, index) => (
                        <div key={item.value} className="chartsTask5" data={item.data} onClick={(event) => { onClickHandlerEnglish(event, index) }} >
                            <span style={{ pointerEvents: 'none' }}>{item.value}</span>
                        </div>
                    ))}
                </div>
                <div className="russionRightColumn">
                    {russianForMathing.map((item, index) => (
                        <div key={item.value} className="chartsTask5" data={item.data} onClick={(event) => { onClickHandlerRussian(event, index) }}>
                            <span style={{ pointerEvents: 'none' }} >{item.value}</span>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default Task5;